          <label for="title">
            标题
          </label>
          <input type="text" id="title">
        </div>
        <div class="bar">
          <label for="content">
            内容
          </label>
          <textarea id="content" cols="60" rows="20"></textarea>
        </div>
        <div class="bar">
          <label for="img">图片</label>
          <img-add class="img-list"></img-add>
        </div>
      </div>
      <a href="javascript:;" class="publish">发布</a>
    </div>
  </div>
</template>

<script>
  import imgAdd from '../img/img-add'
  export default {
    name: 'forumPublish',
